import React, { useState } from "react";
import * as XLSX from "xlsx"; // For reading Excel files
import "./Mockup.css";
import { Form, Stack } from "react-bootstrap";
import CustomButton from "../../components/Button/Button";
import FileUpload from "../../components/FileUploader/FileUploader";
import CustomModal from "../../components/Modal/customModal";
import { downloadTemplate } from "../../utils/helpers";
import useToast from "../../hooks/useToast";

const MassUpload = () => {
  const [modalShow, setModalShow] = useState(false);
  const [operationChoice, setOperationChoice] = useState("");
  const [uploadedData, setUploadedData] = useState(null);
  const [errors, setErrors] = useState([]);
  const { showErrorToast } = useToast();

  // Read XLSX and validate
  const handleFileUpload = async (files) => {
    console.log("üìÇ File selected:", files);

    if (files.length === 0) {
      console.log("‚ö†Ô∏è No file selected.");
      return;
    }

    const file = files[0];
    console.log("üìÑ Reading file:", file.name);

    const reader = new FileReader();

    reader.onload = async (e) => {
      console.log("‚úÖ File successfully read.");

      const data = new Uint8Array(e.target.result);
      const workbook = XLSX.read(data, { type: "array" });

      const sheetName = workbook.SheetNames[0]; // Read first sheet
      console.log("üìú Sheet Name:", sheetName);

      const sheet = workbook.Sheets[sheetName];
      const jsonData = XLSX.utils.sheet_to_json(sheet); // Convert to JSON

      console.log("üìä Parsed XLSX Data:", jsonData);
      setUploadedData(jsonData);

      // Fetch mandatory fields from config.json
      try {
        console.log("üîÑ Fetching config.json...");
        const response = await fetch("/config.json");
        const config = await response.json();
        console.log("‚öôÔ∏è Config Data:", config);

        const mandatoryFields = config.mandatoryFields;
        console.log("üìù Mandatory Fields:", mandatoryFields);

        // Validate required fields
        const missingFields = validateData(jsonData, mandatoryFields);
        if (missingFields.length > 0) {
          console.log("‚ùå Missing Fields Found:", missingFields);
          setErrors(missingFields);
          setModalShow(true);
        } else {
          console.log("‚úÖ All required fields are present.");
        }
      } catch (error) {
        console.error("‚ùå Error fetching config.json:", error);
      }
    };

    reader.readAsArrayBuffer(file);
  };

  // Validate required fields in XLSX
  const validateData = (data, mandatoryFields) => {
    console.log("üîç Validating uploaded data...");
    let missingFields = [];

    data.forEach((row, index) => {
      mandatoryFields.forEach((field) => {
        if (!row[field] || row[field].trim() === "") {
          missingFields.push(`Row ${index + 1}: ${field} is missing`);
        }
      });
    });

    console.log("üîé Validation complete. Missing fields:", missingFields);
    return missingFields;
  };

  const handleDownloadTemplate = () => {
    console.log("‚¨áÔ∏è Downloading template for operation:", operationChoice);
    downloadTemplate(showErrorToast)(operationChoice);
  };

  return (
    <>
      <Stack className="mt-3" gap={4}>
        {/* Choose operation and download template */}
        <Stack direction="horizontal">
          <Form.Select
            aria-label="Choose Operations"
            style={{
              width: "30%",
              border: "1px solid #007bff",
              borderRadius: "8px",
            }}
            onChange={(e) => {
              console.log("üìå Selected Operation:", e.target.value);
              setOperationChoice(e.target.value);
            }}
          >
            <option value="">Choose Operations</option>
            <option value="1">Physical Product/Raw Material</option>
            <option value="2">Physical Product Structure</option>
            <option value="3">Document</option>
            <option value="4">Physical Product-Document</option>
          </Form.Select>

          <CustomButton
            variant="link ms-auto btn-lg"
            onClick={handleDownloadTemplate}
            text="Download Template"
          />
        </Stack>

        {/* Drag and drop container */}
        <FileUpload
          fileTypes={["XLSX"]}
          multiple={false}
          onUpload={handleFileUpload}
        />

        {/* Form-CTA submit */}
        <Stack direction="horizontal">
          <Form.Check type="checkbox" label="background" />
          <CustomButton variant="primary ms-auto" text="Submit" />
        </Stack>
      </Stack>

      {/* Modal to show missing fields */}
      <CustomModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        title="Missing Mandatory Fields"
      >
        {errors.length > 0 ? (
          <ul>
            {errors.map((error, idx) => (
              <li key={idx} style={{ color: "red" }}>
                {error}
              </li>
            ))}
          </ul>
        ) : (
          <p>No errors found</p>
        )}
      </CustomModal>
    </>
  );
};

export default MassUpload;
